import { Component, OnInit, ViewEncapsulation } from '@angular/core';
//RXJS
import { BehaviorSubject } from 'rxjs';
import { first } from 'rxjs/operators';
//Router
import { Router, ActivatedRoute } from '@angular/router';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Service
import { PreviewService } from '../preview.service'

@Component({
  selector: 'kt-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class PreviewComponent implements OnInit {

  context = {
    brief: 3,
    native: 3
  }

  chunk_list = {
    a: new BehaviorSubject(''),
    b: new BehaviorSubject(''),
  }

  deviceInfo = new BehaviorSubject({id:0, ipaddress:''})
  tacacsUsers = new BehaviorSubject([])
  tacacsLog = new BehaviorSubject([])
  tacLogType= 'authe'
  tacUsername= ''

  filePath = ''

  previewType = 'brief'

  loading = new BehaviorSubject(true);

  list = {
    file_a: {
      multiple: false,
      protectDel: true,
      title: 'Version of File A',
      title_sidebar: 'File Versions',
      search: false,
      apiurl: 'api/confmanager/diff/list/',
      extra: {
        file: ''
      }
    },
    file_b: {
      multiple: false,
      protectDel: true,
      title: 'Version of File B',
      title_sidebar: 'File Versions',
      search: false,
      apiurl: 'api/confmanager/diff/list/',
      extra: {
        file: ''
      }
    }
  }

  native_preview = new BehaviorSubject('')

  files = {
    a: {
      version: new BehaviorSubject([]),
      diff: new BehaviorSubject([])
    },
    b: {
      version: new BehaviorSubject([]),
      diff: new BehaviorSubject([])
    }
  }

  constructor(
    private toastr:ToastrService,
    private service: PreviewService,
    private router:Router,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.filePath = params.get('file')
      this.list.file_a.extra.file = params.get('file')
      this.list.file_b.extra.file = params.get('file')
      this.formInit(params.get('file'))
    });
  }

  formInit(file){
    this.service.commitList({file: file}).subscribe( data => {
      console.log(data)
      if (!data.results || data.results.length == 0) {
        this.toastr.error('No commits found')
        return
      }
      this.files.a.version.next([data.results[0]]);
      this.files.b.version.next((data.results[1]) ? [data.results[1]] : [data.results[0]]);
      this.preview()
      //console.log(this.files)
    })
  }

  preview(){
    this.loading = new BehaviorSubject(true);
    this.tacacsLogRun();
    this.files.a.diff.next([])
    this.files.b.diff.next([])
    this.native_preview.next('')
    let styleSheet = document.styleSheets[0] as CSSStyleSheet;
    let data = {
      hash_a: this.files.a.version.getValue()[0].hash,
      hash_b: this.files.b.version.getValue()[0].hash,
      filename: this.filePath,
      // filename_b: 'router_12__1_1',
      type: this.previewType,
      context: this.context[this.previewType] || 3
    }
    this.service.previewInit(data).subscribe( data => {
      this.loading = new BehaviorSubject(false);
      console.log(data)
      if (this.previewType == 'native'){
        this.native_preview.next(data.native)
        return
      }
      if (this.previewType == 'preview'){
        this.native_preview.next(data.show)
        return
      }
      if (!data.diff) {
        this.files.a.diff.next(['No difference'])
        return
      }
      if (data.diff.chunk_list){
        for (let i = 0; i < data.diff.chunk_list.counters.length; i++) {
          console.log(data.diff.chunk_list.counters[i])
          styleSheet.insertRule(
            '.chunk.'+data.diff.chunk_list.counters[i]+':before '+
            '{ counter-increment: '+data.diff.chunk_list.counters[i]+'; content: counter('+data.diff.chunk_list.counters[i]+'); }',
            styleSheet.cssRules.length
          );
        }
        this.chunk_list.a.next(data.diff.chunk_list.file_a.join(' '))
        this.chunk_list.b.next(data.diff.chunk_list.file_b.join(' '))
      }
      this.files.a.diff.next(data.diff.file_a)
      this.files.b.diff.next(data.diff.file_b)
      this.loading = new BehaviorSubject(false);
    })
  }

  tacacsLogRun(){
    this.deviceInfo.next( {id:0, ipaddress:''} )
    this.tacacsUsers.next([])
    if (this.previewType != 'brief' && this.previewType != 'native') return
    this.service.pleaseGiveMeTacacs({
      filename: this.filePath,
      date_a: this.files.a.version.getValue()[0].id,
      date_b: this.files.b.version.getValue()[0].id
    }).subscribe(data => {
      console.log(data)
      if (data.info) this.deviceInfo.next(data.info)
      if (data.users && data.users.length) {
        this.tacacsUsers.next(data.users)
        this.loadUser(data.users[0].username)
      }
      //console.log(this.deviceInfo.getValue())
    })
  }

  changeTacLog(e){
    this.tacLogType = e.nextId;
    this.loadUser(this.tacUsername)
  }
  loadUser(username){
    this.tacUsername = username;
    let data = {
      username: username,
      ip: this.deviceInfo.getValue().ipaddress,
      date_a: this.files.a.version.getValue()[0].id,
      date_b: this.files.b.version.getValue()[0].id,
      section: this.tacLogType
    }
    console.log(data)
    this.service.pleaseGiveMeTacacsLog(data).subscribe( data => {
      console.log(data)
      this.tacacsLog.next(data.log)
    })
  }

  setFile(data, type = 'A'){
    if (type == 'A') this.files.a.version.next(data)
    else this.files.b.version.next(data)
    console.log(data)
    this.preview()
  }

}
