import { Component, OnInit, ViewChild, TemplateRef, ViewContainerRef} from '@angular/core';
import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { ITreeOptions, TreeNode} from 'angular-tree-component';
import { BehaviorSubject, Subscription, fromEvent } from 'rxjs';
import { filter, take } from 'rxjs/operators';
import { TemplatePortal } from '@angular/cdk/portal';
import { NgbModalConfig, NgbModal } from '@ng-bootstrap/ng-bootstrap';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Router
import { Router, ActivatedRoute } from '@angular/router';

//Service
import { PreviewService } from './preview.service'

// interface File_ {
//   name: string,
//   rights?: string
// }
//
// interface Folder_ {
//   name: string,
//   node?: any,
//   rights?: string
// }


@Component({
  selector: 'kt-confmanager',
  templateUrl: './confmanager.component.html',
  styleUrls: ['./confmanager.component.scss']
})
export class ConfmanagerComponent implements OnInit {

  @ViewChild('tree') tree;
  @ViewChild('fileMenu') fileMenu: TemplateRef<any>;

  rootDir = '/opt/tgui_data/confManager/configs'

  overlayRef: OverlayRef | null;
  sub: Subscription;

  validation = new BehaviorSubject([])
  fname = ''
  fname_old = ''
  moveFile = {
    file: {
      name: '',
      path: ''
    },
    new_path: '/'
  }

  options: ITreeOptions = {
    getChildren: this.getChildren.bind(this),
  };

  nodes: any[] = [];

  breadcrumb = {
    path: [],
    nodes: []
  }

  exploer = {
    data: new BehaviorSubject([]),
    loading: new BehaviorSubject(false)
  }

  activeNode: TreeNode

  constructor(
    private service: PreviewService,
    private config: NgbModalConfig,
    private modalService: NgbModal,
    public overlay: Overlay,
    public viewContainerRef: ViewContainerRef,
    private toastr: ToastrService,
    private router:Router,
    private route: ActivatedRoute
  ) {
    config.backdrop = 'static';
    config.keyboard = false;
    this.nodes = [
      {
        name: 'root',
        id: '/',
        path: '/',
        partial_path: [''],
        hasChildren: true,
        deleted: false
      }
    ];
  }

  getChildren(node: any) {
    return this.service.getDir(node.data.path)
  }

  loadChildren(e){
    // console.log(e)
    if (!e.node.children.length) {
      // console.log(e.node)
      e.node.data.hasChildren = false;
    }
  }

  ngOnInit() {

  }

  treeInit(){
    const someNode = this.tree.treeModel.getNodeById('/');
    someNode.expand();

    const firstRoot = this.tree.treeModel.roots[0];
    firstRoot.setActiveAndVisible();
  }

  select(e){
    console.log(e)
    e.node.expand();
    this.activeNode = e.node;
    this.breadcrumb.path = e.node.data.partial_path

    this.breadcrumb.nodes = []
    let counter = 0, parent_temp = e.node;
    while (true) {
      if ( parent_temp.isRoot ) {
        this.breadcrumb.nodes.unshift(parent_temp)
        break;
      }
      parent_temp = parent_temp.parent
      this.breadcrumb.nodes.unshift(parent_temp)
      counter++
      if ( parent_temp.isRoot ) break;
      if ( counter > 1000) break;
    }
    this.fillExploer(e.node)
    // console.log(this.breadcrumb)
  }

  fillExploer(node){
    this.exploer.loading.next(true)
    this.exploer.data.next([])

    this.service.getDirExploer(node.data.path).subscribe( data => {
      // console.log(data)
      let els = []

      for (let i = 0; i < data.length; i++) {
        els.push(this.createElement(data[i]))
      }
      if (!node.isRoot)
        els.unshift({ name: node.parent.data.name, dir: true, icon: 'fas fa-angle-up', parent: true })
      this.exploer.data.next(els)
      this.exploer.loading.next(false)
    })
  }

  createElement(el){
    let regex = RegExp('^d.*');
    let dir = regex.test(el)
    let parts = el.split(' ');

    return {
      name: parts[1],
      path: this.breadcrumb.path.join('/') + '/' + parts[1],
      dir: dir,
      icon: (dir) ? 'fa fa-folder' : 'fa fa-file-alt',
    }
  }

  navigatorTree(index){
    // console.log(index)
    this.breadcrumb.nodes[index].expand();
    this.breadcrumb.nodes[index].setActiveAndVisible();
  }

  elClick(el){
    // console.log(el)
    if (!el.dir){
      this.preview(el.path)
      return
    }
    if ( el.parent ) {
      this.activeNode.parent.expand();
      this.activeNode.parent.setActiveAndVisible();
      return
    }
    if ( el.dir ) {
      let superDone = false

      for (let i = 0; i < this.activeNode.children.length; i++) {
          // console.log(this.activeNode.children[i])
          // console.log(this.activeNode.children[i].data.name == el.name)
          if (this.activeNode.children[i].data.name == el.name) {
              this.activeNode.children[i].expand();
              this.activeNode.children[i].setActiveAndVisible();
              return
          }
      }
      // console.log(this.activeNode.children)
      return
    }
    console.log(this.activeNode)
  }

  openContext(e: MouseEvent, el){
    console.log(el)
    this.closeContext();
    const positionStrategy = this.overlay.position()
      .flexibleConnectedTo(e)
      .withPositions([
        {
          originX: 'end',
          originY: 'bottom',
          overlayX: 'end',
          overlayY: 'top',
        }
      ]);

    this.overlayRef = this.overlay.create({
      positionStrategy,
      scrollStrategy: this.overlay.scrollStrategies.close()
    });

    this.overlayRef.attach(new TemplatePortal(this.fileMenu, this.viewContainerRef, {
      $implicit: el
    }));

    this.sub = fromEvent<MouseEvent>(document, 'click')
      .pipe(
        filter(event => {
          const clickTarget = event.target as HTMLElement;
          return !!this.overlayRef && !this.overlayRef.overlayElement.contains(clickTarget);
        }),
        take(1)
      ).subscribe(() => this.closeContext())

  }

  closeContext() {
    this.sub && this.sub.unsubscribe();
    if (this.overlayRef) {
      this.overlayRef.dispose();
      this.overlayRef = null;
    }
  }

  preview(file){
    console.log(file)
    this.router.navigate(['./preview', file], { relativeTo: this.route });
  }

  newFolder(content){
    this.fname = ''
    this.modalService.open(content);
  }

  addFolder(tree){

    let path = this.breadcrumb.path.join('/')+'/'+this.fname
    this.service.addFolder(
      {
        fname: this.fname,
        parent: this.rootDir + this.breadcrumb.path.join('/')+'/',
        path: this.rootDir + path,
      }).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        //this.loading.next(false)
        return
      }
      this.activeNode.data.children = this.activeNode.data.children || []
      this.activeNode.data.children.unshift(
        {
          name: this.fname,
          path: path,
          partial_path: path.split('/')
        }
      )
      tree.treeModel.update();
      tree.treeModel.getNodeById(this.activeNode.data.id)
        .setActiveAndVisible();
      this.modalService.dismissAll();
    })
  }

  deleteFile(f, tree){
    // console.log(f)
    if (! confirm('Are you sure? Delete '+f.name+'?') ) return
    this.closeContext()
    this.service.delFolder(
      {
        path: this.rootDir + f.path,
        file: !!f.dir,
      }).subscribe( data => {
      console.log(data)
      if (!data.status){
        this.toastr.error('Error')
        return
      }
      for (let i = 0; i < this.exploer.data.getValue().length; i++) {
        if ( f !== this.exploer.data.getValue()[i] ) continue;
        this.exploer.data.pipe(take(1)).subscribe( (data) => {
          data.splice(i, 1)
          this.exploer.data.next(data)
        })
        break;
      }
      if (f.dir) {
        for (let i = 0; i < this.activeNode.children.length; i++) {
          console.log(f.name !== this.activeNode.children[i].data.name)
          if ( f.name !== this.activeNode.children[i].data.name ) continue;
          this.activeNode.children[i].parent.data.children.splice(i, 1)
          tree.treeModel.update();
          tree.treeModel.getNodeById(this.activeNode.data.id)
            .setActiveAndVisible();
          // console.log(this.activeNode)
          //this.activeNode = e.node;
          break;
        }
      }
      this.toastr.success('Deleted')
    })
  }

  renameOpen(file, content){
    this.fname = file.name;
    this.fname_old = file.name;
    this.modalService.open(content);
    this.closeContext()
  }

  renameFolder(){
    console.log(this.fname, this.fname_old)
    this.service.moveFolder(
      {
        fname: this.fname,
        path: this.rootDir + this.breadcrumb.path.join('/')+'/'+this.fname,
        path_old: this.rootDir + this.breadcrumb.path.join('/')+'/'+this.fname_old,
      }
    ).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        //this.loading.next(false)
        return
      }
      this.tree.treeModel.update();
      let myTarget = this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/'+this.fname_old+'/')
      console.log(myTarget)
      myTarget.data.name = this.fname
      myTarget.data.path = this.breadcrumb.path.join('/')+'/'+this.fname+'/'
      myTarget.data.id = this.breadcrumb.path.join('/')+'/'+this.fname+'/'
      this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/')
        .setActiveAndVisible();
      this.modalService.dismissAll();
    })
  }

  openMove(file, content){
    console.log(file)
    this.moveFile.file = file
    this.modalService.open(content);
    this.closeContext()
  }

  newPathSelect(e:string){
    console.log(e)
    this.moveFile.new_path = e || '/'
  }

  moveFolder(){
    this.service.moveFolder(
      {
        fname: this.moveFile.file.name,
        path: this.rootDir + this.moveFile.new_path+this.moveFile.file.name,
        path_old: this.rootDir + this.breadcrumb.path.join('/')+'/'+this.moveFile.file.name,
      }
    ).subscribe( data => {
      console.log(data)
      console.log(this.breadcrumb.path.join('/')+'/'+this.moveFile.file.name+'/')
      let node_child = this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/'+this.moveFile.file.name+'/')
      let node_old_parent = this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/')
      console.log(node_child)
      console.log(node_old_parent)
      node_child.data.deleted = true
      node_child.data.id = node_child.data.id + '--deleted'

      // _.remove(node_old_parent.parent.data.children, node_child.data);
      // for (let i = 0; i < node_old_parent.data.children.length; i++) {
      //     if (node_old_parent.data.children[i].name !== node_child.data.name) continue;
      //     node_old_parent.data.children.splice(i,1)
      //     node_old_parent.children.splice(i,1)
      //     break;
      // }
      //this.tree.treeModel.update();
      // this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/')
      //   .setActiveAndVisible();

      let node_new_parent = this.tree.treeModel.getNodeById(this.moveFile.new_path)
      console.log(node_new_parent)

      if (node_new_parent && node_new_parent.data.children) {
        let new_data = JSON.parse(JSON.stringify({
          id: this.moveFile.new_path+this.moveFile.file.name + '/',
          name: this.moveFile.file.name,
          path: this.moveFile.new_path+this.moveFile.file.name + '/',
          partial_path: this.moveFile.new_path.split('/'),
          hasChildren: true
        }));
        // let new_data = { name: 'Jopa!!!' }
        console.log(new_data)
        node_new_parent.data.children.push(new_data)
      }
      // console.log(node_new_parent)
      //node_new_parent.data.children.unshift()
      this.tree.treeModel.update();
      this.tree.treeModel.getNodeById(this.breadcrumb.path.join('/')+'/')
        .setActiveAndVisible();
      this.modalService.dismissAll();
    })

  }


}
